import { __ } from '@wordpress/i18n';
import { useBlockProps, RichText } from '@wordpress/block-editor';
import { Button, TextControl } from '@wordpress/components';
import './editor.scss';

export default function Edit({ attributes, setAttributes }) {
    const { heading, description, videoUrl, backgroundColor, style } = attributes;

    const getBackgroundColor = () => backgroundColor || style?.color?.background || '#f8fafc';

    const blockProps = useBlockProps({
        style: {
            backgroundColor: getBackgroundColor(),
            padding: '60px 40px',
        },
    });

    const getIframeSrc = (url) => {
        if (!url) return '';
        // YouTube
        if (url.includes('youtube.com') || url.includes('youtu.be')) {
            const videoId =
                url.split('v=')[1]?.split('&')[0] || url.split('youtu.be/')[1]?.split('?')[0];
            return videoId ? `https://www.youtube.com/embed/${videoId}?rel=0&modestbranding=1` : '';
        }
        // Vimeo
        if (url.includes('vimeo.com')) {
            const videoId = url.split('/').pop();
            return videoId ? `https://player.vimeo.com/video/${videoId}` : '';
        }
        return url || '';
    };

    const iframeSrc = getIframeSrc(videoUrl) || '';

    return (
        <div {...blockProps}>
            <div className="genetic-testing-block">
                <div className="genetic-content">
                    <RichText
                        tagName="h2"
                        value={heading}
                        onChange={(value) => setAttributes({ heading: value })}
                        placeholder="What is genetic testing?"
                        className="genetic-heading"
                    />
                    <RichText
                        tagName="div"
                        multiline="p"
                        value={description}
                        onChange={(value) => setAttributes({ description: value })}
                        placeholder="Genetic testing studies your genes..."
                        className="genetic-description"
                    />
                </div>

                <div className="genetic-video">
                    <TextControl
                        label="Video URL (YouTube/Vimeo)"
                        value={videoUrl}
                        onChange={(value) => setAttributes({ videoUrl: value || '' })}
                        help="Paste YouTube/Vimeo URL - auto embeds"
                        placeholder="https://www.youtube.com/watch?v=..."
                    />

                    {iframeSrc && (
                        <div className="video-container">
                            <iframe
                                src={iframeSrc}
                                title="Genetic testing video"
                                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                allowFullScreen
                                className="video-iframe"
                            />
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}
